      ),
    };
  },
  components: {
    AisRangeInput,
    VueSlider,
  },
  methods: {
    toValue(value, range) {
      return [
        typeof value.min === 'number' ? value.min : range.min,
        typeof value.max === 'number' ? value.max : range.max,
      ];
    },
  },
};
